body {
    margin: 0px;
    padding: 0px;
    font-family: Calibri;
    /*animation: background-rainbow 10s ease infinite;*/
}

ul, menu {
    margin: 0px;
    padding: 0px;
    list-style-type: none;
}

a, button {
    user-select: none;
    cursor: pointer;
    text-decoration: none;
}

img {
    user-select: none;
}

p {
    margin: 0px;
    text-decoration: none;
    white-space: pre-wrap;
}

.drawline {
    height: 500px;
    width: 2px;
    background-color: white;
    margin: 380px 125px;
    z-index: 100;
    position: absolute;
}

/*color palet*/
:root {
    --yellow: #ffc913;
    --background: #1a2d3b;
    --dark-blue: #156ab8;
}
/*End color palet*/

@keyframes background-rainbow {
    0% {
        background-color: #ff0000; /* red */
    }

    14.3% {
        background-color: #ff8000; /* orange */
    }

    28.6% {
        background-color: #ffff00; /* yellow */
    }

    42.9% {
        background-color: #00ff00; /* green */
    }

    57.1% {
        background-color: #00ffff; /* blue */
    }

    71.4% {
        background-color: #0000ff; /* indigo */
    }

    85.7% {
        background-color: #ff00ff; /* violet */
    }

    100% {
        background-color: #ff0000; /* red */
    }
}
