@keyframes hud-blurry-background {
    0% {
        backdrop-filter: blur(0px);
    }

    100% {
        backdrop-filter: blur(3px);
    }
}

/*BlurryBackground*/
.hud div.hud-blurry-background {
    position: absolute;
    top: 0px;
    height: 100vh;
    width: 100vw;
    animation: hud-blurry-background 1s ease;
    backdrop-filter: blur(3px);
}
/*End BlurryBackground*/
