@import './hud.css';

@keyframes hud-header-appear {
    0% {
        transform: translateY(0px);
    }

    100% {
        transform: translateY(87px);
    }
}

@keyframes hud-header-disappear {
    0% {
        transform: translateY(87px);
    }

    100% {
        transform: translateY(0px);
    }
}

.hud section.hud-header {
    height: 100px;
}

    .hud section.hud-header img.triangle {
        position: absolute;
        height: 10px;
        width: 8px;
        bottom: -4px;
        z-index: 2;
    }

        .hud section.hud-header img.triangle.right {
            right: 0px;
            transform: scaleX(-1);
        }

    .hud section.hud-header div.header-border {
        position: absolute;
        top: -87px;
        height: 100px;
        width: 100%;
        z-index: 2;
        border-radius: 0px 0px 10px 10px;
        background-color: var(--yellow);
    }

        .hud section.hud-header div.header-border.on-to-off {
            animation: hud-header-disappear 0.2s ease-in-out;
            transform: translateY(0px);
        }

        .hud section.hud-header div.header-border.off-to-on {
            animation: hud-header-appear 0.4s ease-in-out;
            transform: translateY(87px);
        }

        .hud section.hud-header div.header-border.on-locked {
            transform: translateY(87px);
        }

        .hud section.hud-header div.header-border.off-locked {
            transform: translateY(0px);
        }

        .hud section.hud-header div.header-border div.header-background {
            height: 97px;
            border-radius: 0px 0px 10px 10px;
            background-color: black;
            display: flex;
            justify-content: space-between;
        }

            .hud section.hud-header div.header-border div.header-background div.left-container {
                height: 100%;
                display: flex;
                align-items: flex-end;
            }

                .hud section.hud-header div.header-border div.header-background div.left-container a.name, a.occupation {
                    color: var(--text);
                }

                .hud section.hud-header div.header-border div.header-background div.left-container a.img {
                    padding: 0px 0px 0px 33px;
                    margin: auto 0;
                    height: 67px;
                    width: 67px;
                    align-content: center;
                }

                    .hud section.hud-header div.header-border div.header-background div.left-container a.img img {
                        height: 100%;
                        width: 100%;
                    }

                .hud section.hud-header div.header-border div.header-background div.left-container a.name {
                    margin: 0px 0px 10px 27px;
                    font-size: 40px;
                    font-weight: bold;
                }

@media (max-width: 860px) {
    .hud section.hud-header div.header-border div.header-background div.left-container a.name {
        font-size: 30px;
        width: 120px;
    }
}


.hud section.hud-header div.header-border div.header-background div.left-container a.occupation {
    margin: 0px 0px 13px 13px;
    font-size: 20px;
}

.hud section.hud-header div.header-border div.header-background menu {
    width: 469px;
    margin: 0px 0px 13px 0px;
    display: flex;
    align-items: flex-end;
    justify-content: space-evenly;
}

    .hud section.hud-header div.header-border div.header-background menu a {
        padding: 0px 5px;
        font-size: 23px;
        color: var(--text);
    }

    .hud section.hud-header div.header-border div.header-background menu button {
        padding: 0px 5px 2px 5px;
        font-size: 20px;
        color: var(--text);
        background-color: transparent;
        border: none;
    }

        .hud section.hud-header div.header-border div.header-background menu a:hover,
        .hud section.hud-header div.header-border div.header-background menu button:hover {
            color: gray;
        }
