::-webkit-scrollbar {
    display: none;
}

/* Internet Explorer */
*::-ms-scrollbar-thumb {
    display: none;
}

/*color palet*/
:root {
    --text: white;
}
/*End color palet*/

.hud {
    position: fixed;
    width: 100vw;
    z-index: 10;
}

.hud a, .hud button {
    cursor: pointer;
}
